/* src/Home.css */
.home {
    text-align: center;
    padding: 20px;
  }
  
  .sample-table {
    margin: 0 auto;
    border-collapse: collapse;
    width: 80%;
  }
  
  .sample-table th, .sample-table td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .sample-table th {
    background-color: #f2f2f2;
    text-align: center;
  }
  
  .sample-table td {
    text-align: center;
  }
  
  .grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin: 20px auto;
    width: 80%;
  }
  
  .grid-item {
    background-color: #ccc;
    padding: 20px;
    text-align: center;
    font-size: 20px;
  }
  
  .adsbygoogle {
    margin: 20px auto;
  }
  